<!-- by 添少 -->
<template>
  <section>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="detail.mainimg"/>
      </div>
      <div class="text">
        <h3>{{detail.name}}</h3>
        <p class="info">{{`${detail.age}岁 ${detail.city} ${detail.lang}`}}</p>
        <p>{{detail.desc}}</p>
      </div>
    </section>
    <section class="layout">
      <ul class="ui-group">
        <mt-cell title="加入的俱乐部" :value="sheetClub.list[0]&&sheetClub.list[0].name||'无'" is-link
                 @click.native="sheetClub.show=true"></mt-cell>
      </ul>
    </section>
    <mt-actionsheet :actions="sheetClub.list" v-model="sheetClub.show"></mt-actionsheet>
  </section>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    name: 'expert',
    components: {
    },
    data() {
      return {
        detail: {},
        clubJoinList: [],
        sheetClub: {
          show: false,
          list: []
        }
      }
    },
    computed: {
      ...mapState({}),
    },
    created() {
      this.init()
    },
    methods: {
      init() {
        this.$store.commit(this.$actionType.set_detail_title, {
          title: '达人'
        })
        if (this.$route.query.expertId) {
          this.$http.get(this.$port.GET_EXPERT_DETAIL, {
            params: {
              expertId: this.$route.query.expertId
            }
          }).then(res => {
            if (res.code === 200) {
              this.detail = res.data
              res.data.clubJoinList.forEach(e => {
                this.sheetClub.list.push({
                  name: e.name,
                  method: () => {
                    this.$router.push({
                      path: '/detail/club',
                      query: {
                        clubId: e.clubId
                      }
                    })
                  }
                })
              })
            }
          })
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px 10px;
      margin-bottom: 10px;
    }
    .info {
      font-size: 14px;
      color: #888;
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 10px;
    }
  }

</style>



